export default {
	data() {
		return {
			zhuti_id:0,
			zhutitupian:'../../../static/img/alumni/red.png',
			zhuti:[
				'../../../static/img/alumni/red.png',
				'../../../static/img/alumni/purple.png',
				'../../../static/img/alumni/yellow.png',
				'../../../static/img/alumni/blue.png',
				'../../../static/img/alumni/cyan.png',
				'../../../static/img/alumni/green.png',
				'../../../static/img/alumni/orange.png'
			],
			isfirst:true,
			headerPosition:"fixed",
			headerTop:null,
			statusTop:null,
			showHeader:true,
			//个人信息,
			user:{
				id:'00000000000',
				name:'***',
				face:'/static/img/face.jpg',
				signature:'个性签名',
				is_authentication:"0",
				sfzh:'',
				zuigaoxueli:'',
				is_authentication:''
			},
			// 显示学院
			viewCollege:{
				byrq: "2020",
				college: "***",
				id: "***",
				major: "***",
				rxrq: "***",
			},
		// 最高学历
		zuigaoxueli:'本科',
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	onLoad() {
		var self=this;
		this.statusHeight = 0;




		
		//验证是否已经登录
		if(global.islogin()==false){
			// 未登录就跳转到login页面，在全局去定义islogin函数
			uni.reLaunch({
				url:'../../login/login'
			})
		}else{
			//登录就发送Token到后端s 李杰  
			uni.request({
				url:self.$url+'userinfo/index',
				method:'POST',
				header:{
					token:global.islogin()
				},
				success:function(res){
					if(res.statusCode==200){
						self.user=res.data.data;
						self.zuigaoxueli=res.data.data.zuigaoxueli;
						switch(self.zuigaoxueli){
							case '本科':self.viewCollege=res.data.data.benke;break;
							case '专科':self.viewCollege=res.data.data.zhuanke;break;
							case '硕士':self.viewCollege=res.data.data.shuoshi;break;
							case '博士':self.viewCollege=res.data.data.boshi;break;
							self.viewCollege=res.data.data.hanshou;break;
						}
					}
				},
				complete:function(res){//token若过期  李杰  token过期这里已经做了 
					if(res.data.code==0){
						//跳转到登录页面
						uni.reLaunch({
							url:'../../login/login'
						})
					}
				}
			})
		}			
	},
	onReady(){
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function () {
			},
			fail:function(e){
			}
		});
	},
	onShow(){
		uni.getStorage({
			key: 'UserInfo',
			success: (res)=>{
				if(!res.data){
					if(this.isfirst){
						//this.toLogin();
					}
					return ;
				}
				this.user = res.data;
			},
			fail:(e)=>{
				//this.toLogin(); 
			}
		});
	},
	methods: {
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		toOrderList(index){
			uni.setStorageSync('tbIndex',index);
			uni.navigateTo({url:'../../user/order_list/order_list?tbIndex='+index}) 
		},
		toSetting(){
			uni.navigateTo({
				// url:'../../user/setting/setting'
				url:'../../user/Improveinfo/Improveinfo'
			})
		},
		toMyQR(){
			uni.navigateTo({
				url:'../../user/myQR/myQR'
			})
		},
		toLogin(){
			uni.showToast({title: '请登录',icon:"none"});
			uni.navigateTo({
				url:'../../login/login'
			})
			this.isfirst = false;
		},
		isLogin(){
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit(){
			uni.navigateTo({
				url:'../../user/deposit/deposit'
			})
		},
		toPage(url){
			if(!url){
				uni.showToast({title: '模板未包含此页面',icon:"none"});return;
			}
			uni.navigateTo({
				url:url
			})
		},
		// 切换主题
		toChangezhuti(){
			if(this.zhuti_id<this.zhuti.length-1){
				this.zhuti_id++;
			}else{
				this.zhuti_id=0;
			}
			this.zhutitupian=this.zhuti[this.zhuti_id]
		}
	}
} 
